*{
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

.item-inside{
  background-color: aliceblue;
  box-shadow: 0 5px 15px rgba(0,0,0,0.8);
  width: 22rem;
}
.img-div img{
  width: 80px;
  transform-origin: initial;
  transform: translate(15px ,12px);
}

input{
  display: block;
  max-width: 350px;
  width: 100%;
  margin: auto;
  border: none;
  outline: none;
  cursor: pointer;
  border-image: linear-gradient(#fdcb2c, #f9863f) 1;
  border-width: 2px;
  border-style: solid;
  object-fit: cover;
  padding: 10px 10px;
  border-radius: 10px;
  font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
  text-transform: capitalize;
  margin-top: 20px;
}
.bti{
  background-color: crimson;
  border: none;
  outline: none;
  padding: 10px 16px;
  text-transform: capitalize;
  color: white;
  border-radius: 5px;
  font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
}

@media screen and (max-width:497px) {
  .justify-content-around{
    flex-direction: column;
  }
  .bti{
    margin: 5px 0px;
  }
}